.Bar {
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  width: 90%;
  margin: 5px;
}

.Bar .info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.Bar .progress {
  border: 2px solid black;
  min-width: 100%;
  border-radius: 4px;
  background: rgb(61, 170, 212);
}

.Bar .progress:before {
  content: '';
  position: absolute;
  border-top-right-radius: inherit;
  border-bottom-right-radius: inherit;
  right: -3px;
  bottom: 1px;
  height: inherit;
  width: inherit;
  max-width: 100%;
  border: 1px solid black;
  background: grey;
}

.Bar .progress.full:before {
  content: none;
}

.Bar .progress.empty:before {
  border-radius: inherit;
}


