.Header {
  position: absolute;
  top: 0;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  background-color: var(--dark);
  height: 75px;
  color: white;
}

.Header .title {
  font-size: 2rem;
}

@media screen and (min-width: 635px) {
  .Header {
    -ms-flex-pack: center;
        justify-content: center;
  }
}

.Header button {
  position: absolute;
  right: 10px;
}


