      flex: 1 1 auto;
  -webkit-transition: opacity 1000ms ease-in-out, visibility 0ms linear 1000ms;
  -o-transition: opacity 1000ms ease-in-out, visibility 0ms linear 1000ms;
  transition: opacity 1000ms ease-in-out, visibility 0ms linear 1000ms;
}

.Character.size-medium {
